import svg4everybody from 'svg4everybody';
import $ from 'jquery';
import 'slick-carousel';
import '../../node_modules/mixitup/build/jquery.mixitup.min.js';

$(() => {
	svg4everybody();
	$('.reviews__list').slick({
		dots: true,
		infinite: true,
		speed: 300,
		slidesToShow: 1,
		arrows: false,
		customPaging: function () {
			return '<a class="reviews__dot"></a>';
		}
	});
	$('.works__list').mixItUp();
	$('.banner__container').slick({
		infinite: false,
		speed: 300,
		arrows: true,
		nextArrow: '<div class="banner__arrow-left"></div>',
	    prevArrow: '<div class="banner__arrow-right"></div>'
	});
});



